@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "ShantellSans";
  font-display: swap;
  src: url("../fonts/shantellsans-medium.woff2") format("woff2"), url("../fonts/shantellsans-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "ShantellSans";
  font-display: swap;
  src: url("../fonts/shantellsans-semibold.woff2") format("woff2"), url("../fonts/shantellsans-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "ShantellSans";
  font-display: swap;
  src: url("../fonts/shantellsans-bold.woff2") format("woff2"), url("../fonts/shantellsans-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  font-family: "ShantellSans", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
  user-select: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
}

.wrapper {
  position: relative;
  display: flex;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.resume {
  display: grid;
  font-size: 1.5vw;
  grid-template-columns: 36% 64%;
}
@media (max-width: 1023px) {
  .resume {
    font-size: 3vw;
    grid-template-columns: 1fr;
  }
}

.resume__photo {
  background-color: #2d2f42;
  border: 1px solid #e8ba00;
  grid-area: 1/1/2/2;
}
.resume__photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.resume__about {
  grid-area: 1/2/3/3;
}
@media (max-width: 1023px) {
  .resume__about {
    grid-area: auto;
  }
}

.resume__info {
  grid-area: 2/1/3/2;
}
@media (max-width: 1023px) {
  .resume__info {
    grid-area: auto;
  }
}

.about {
  background-color: #070709;
}

.about__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 7% 2%;
  background-color: #e8ba00;
  gap: 8px 0;
}

.about__title {
  margin: 0;
  font-weight: 500;
  font-size: 4vw;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.about__title span {
  font-weight: 700;
}
@media (max-width: 1023px) {
  .about__title {
    font-size: 5.5vw;
  }
}

.about__profession {
  margin: 0;
  font-size: 2vw;
  text-transform: uppercase;
  letter-spacing: 5px;
}
@media (max-width: 1023px) {
  .about__profession {
    font-size: 3.5vw;
  }
}

.about__level {
  margin: 0;
  font-size: 2vw;
  text-transform: uppercase;
  letter-spacing: 5px;
}
@media (max-width: 1023px) {
  .about__level {
    font-size: 3.5vw;
  }
}

.about__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 7% 10%;
  list-style-type: none;
  gap: 3vw 0;
}
@media (max-width: 1023px) {
  .about__list {
    gap: 5vw 0;
  }
}

.about__item {
  display: flex;
  flex-direction: column;
  gap: 1.8vw 0;
}

.about__subtitle {
  position: relative;
  margin: 0;
  font-weight: 600;
  font-size: 2vw;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.about__subtitle::before {
  content: "";
  position: absolute;
  bottom: -0.8vw;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #e8ba00;
}
@media (max-width: 1023px) {
  .about__subtitle {
    font-size: 3.5vw;
  }
}

.about__description-group {
  display: flex;
  flex-direction: column;
  letter-spacing: 0.5px;
  gap: 2.5vw 0;
}
.about__description-group ul {
  list-style-type: disc;
}

.about__description {
  margin: 0;
}

.about__description:last-child {
  margin: 0;
}

.about__stature-list {
  padding-left: 20px;
}
.about__stature-list a {
  transition: color 0.3s ease;
}
.about__stature-list a:hover:not([disabled]) {
  color: #e8ba00;
}
.about__stature-list a:focus-visible:not([disabled]) {
  color: #e8ba00;
}
.about__stature-list a:active:not([disabled]) {
  color: rgba(232, 186, 0, 0.7);
}

.about__experience-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 2.5vw 0;
}

.about__experience-item {
  display: grid;
  gap: 0 3vw;
  grid-template-columns: auto 1fr;
}

.about__experience-group {
  display: flex;
  flex-direction: column;
  gap: 0.5vw 0;
  grid-area: 1/2/2/3;
}

.about__position {
  margin: 0;
  font-weight: 700;
  text-transform: uppercase;
}

.about__company {
  margin: 0;
}

.about__time {
  grid-area: 1/1/2/2;
}

.about__technology-list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 1vw;
  grid-template-columns: repeat(2, 1fr);
}
.about__technology-list li {
  display: flex;
}
.about__technology-list li::before {
  content: "➣";
  padding-right: 5px;
  color: #e8ba00;
}

.about__project-list {
  display: grid;
  margin: 0.5vw 0 0;
  padding: 0;
  list-style-type: none;
  gap: 1vw;
  grid-template-columns: repeat(3, 1fr);
}

.about__project-link {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.about__project-link img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__project-link:hover:not([disabled]) {
  opacity: 0.6;
}
.about__project-link:focus-visible:not([disabled]) {
  opacity: 0.6;
}
.about__project-link:active:not([disabled]) {
  opacity: 0.3;
}

.info {
  display: flex;
  flex-direction: column;
  padding: 12% 7% 12% 13%;
  color: #ffffff;
  background-color: #202026;
}
@media (max-width: 1023px) {
  .info {
    padding: 7% 10%;
  }
}

.info__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 3vw 0;
}
@media (max-width: 1023px) {
  .info__list {
    gap: 5vw 0;
  }
}

.info__item {
  display: flex;
  flex-direction: column;
  gap: 1.8vw 0;
}

.info__subtitle {
  position: relative;
  margin: 0;
  font-weight: 600;
  font-size: 2vw;
  color: #e8ba00;
  text-transform: uppercase;
  letter-spacing: 3px;
}
.info__subtitle::before {
  content: "";
  position: absolute;
  bottom: -0.8vw;
  width: 108.7%;
  height: 2px;
  background-color: #e8ba00;
}
@media (max-width: 1023px) {
  .info__subtitle {
    font-size: 3.5vw;
  }
  .info__subtitle::before {
    width: 113%;
  }
}

.info__educations-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0 0 0 20px;
  list-style-type: none;
  gap: 2.5vw 0;
}
.info__educations-list li {
  display: flex;
  flex-direction: column;
  gap: 0.6vw 0;
}

.info__header {
  margin: 0;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.info__header a {
  transition: color 0.3s ease;
}
.info__header a:hover:not([disabled]) {
  color: #e8ba00;
}
.info__header a:focus-visible:not([disabled]) {
  color: #e8ba00;
}
.info__header a:active:not([disabled]) {
  color: rgba(232, 186, 0, 0.7);
}

.info__education-name {
  display: block;
  font-size: 1.3vw;
  transition: color 0.3s ease;
}
.info__education-name:hover:not([disabled]) {
  color: #e8ba00;
}
.info__education-name:focus-visible:not([disabled]) {
  color: #e8ba00;
}
.info__education-name:active:not([disabled]) {
  color: rgba(232, 186, 0, 0.7);
}
@media (max-width: 1023px) {
  .info__education-name {
    font-size: 2.8vw;
  }
}

.info__time {
  font-size: 1.3vw;
}
@media (max-width: 1023px) {
  .info__time {
    font-size: 2.8vw;
  }
}

.info__certificates-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0 0 0 20px;
  list-style-type: none;
  font-size: 1.3vw;
  gap: 2vw 0;
}
.info__certificates-list a {
  transition: color 0.3s ease;
}
.info__certificates-list a:hover:not([disabled]) {
  color: #e8ba00;
}
.info__certificates-list a:focus-visible:not([disabled]) {
  color: #e8ba00;
}
.info__certificates-list a:active:not([disabled]) {
  color: rgba(232, 186, 0, 0.7);
}
@media (max-width: 1023px) {
  .info__certificates-list {
    font-size: 2.8vw;
  }
}

.info__contacts-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0 0 0 20px;
  list-style-type: none;
  gap: 2vw 0;
}
.info__contacts-list li {
  display: flex;
  flex-direction: column;
  gap: 0.6vw 0;
}

.info__contact-title {
  position: relative;
  z-index: 0;
  margin: 0;
  font-weight: 600;
}
.info__contact-title::before {
  content: "";
  position: absolute;
  top: -2%;
  left: -30%;
  z-index: -1;
  width: 139%;
  height: 104%;
  background-color: #292a30;
}
.info__contact-title::after {
  content: "";
  position: absolute;
  top: -2%;
  left: -30%;
  z-index: -1;
  width: 25%;
  height: 104%;
  background-color: #e8ba00;
}
@media (max-width: 1023px) {
  .info__contact-title::before {
    width: 144%;
  }
}

.info__contact-link {
  font-size: 1.3vw;
  transition: color 0.3s ease;
}
.info__contact-link:hover:not([disabled]) {
  color: #e8ba00;
}
.info__contact-link:focus-visible:not([disabled]) {
  color: #e8ba00;
}
.info__contact-link:active:not([disabled]) {
  color: rgba(232, 186, 0, 0.7);
}
@media (max-width: 1023px) {
  .info__contact-link {
    font-size: 2.8vw;
  }
}

